<script>
import axios from "axios";

export default {
  name: "register",
  data() {
    let id = 0;
    return {
      form: {
        phone: "",
        name: "",
        email: "",
        birthday: "",
        address: {
          value: "",
          values: [],
          addressList: {
            lazy: true,
            lazyLoad(node, resolve) {
              if (!node.label) {
                axios({
                  methods: "get",
                  url:
                    "https://script.google.com/macros/s/AKfycbyAxehtbFWqje0TGMRMKteOvE2s0KCsVEboOVOGGjK5W6oQSdz_/exec?city=all"
                }).then(resp => {
                  const nodes = resp.data.data.map(item => ({
                    value: item,
                    label: item,
                    leaf: false
                  }));
                  resolve(nodes);
                });
              } else {
                axios({
                  methods: "get",
                  url:
                    "https://script.google.com/macros/s/AKfycbyAxehtbFWqje0TGMRMKteOvE2s0KCsVEboOVOGGjK5W6oQSdz_/exec?city=" +
                    node.label
                }).then(resp => {
                  const nodes = resp.data.data.map(item => ({
                    value: item,
                    label: item,
                    leaf: true
                  }));
                  resolve(nodes);
                });
              }
            }
          }
        }
      }
    };
  },
  created: function() {
    this.$store.dispatch("getLineId");
  },
  watch: {
    "form.address.values": {
      handler(newName, oldName) {
        this.form.address.value = this.form.address.values
          .toString()
          .replace(",", "");
      },
      immediate: true,
      deep: true
    }
  },
  computed: {
    lineId() {
      return this.$store.state.lineId;
    },
    addressChoose: function() {
      return this.form.address.value == "";
    },
    canSent: function() {
      return (
        this.form.address.value != "" &&
        this.form.phone != "" &&
        this.form.name != "" &&
        this.form.email != "" &&
        this.form.birthday != ""
      );
    }
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>

<template src="./template.html"></template>
<style lang="css" src="./style.css" scoped></style>
